.name {
  width: 350px;
  max-height: 50px ;
  margin: 5px;
  border: 2px solid;
  font-size: 120%;
  font-style: italic;
  color: white;
  background-color: #254bed;
  text-align: center;
}

.animation{
  -webkit-animation: blink 2s linear infinite;
  animation: blink 2s linear infinite;
}

@-webkit-keyframes blink {
  50% { color: rgb(34, 34, 34); }
  51% { color: rgba(34, 34, 34, 0); }
  100% { color: rgba(34, 34, 34, 0); }
}

@keyframes blink {
  50% { color: rgb(34, 34, 34); }
  51% { color: rgba(34, 34, 34, 0); }
  100% { color: rgba(34, 34, 34, 0); }
}